import { useState } from "react";
import Navbar from "../Conponents/Navbar";
import Footer from "../Conponents/Footer"
import Filter from "../Parts/Product/Filter";
import ListProduct from "../Parts/Product/ListProduct";

const Product = () => {
  const [datasFromchild,setDatasFromChild] = useState({})
  return (
    <>
      <Navbar />
      <section className="px-44 flex gap-5 pt-[150px] pb-20">
        <Filter datasToParent={setDatasFromChild}/>
        <ListProduct datas={datasFromchild} />
      </section>
      <Footer />
    </>
  );
};

export default Product;
