//Component
import { useParams } from "react-router-dom"
import Navbar from "../Conponents/Navbar"
//Library external
import Hashids from "hashids";
import { useEffect } from "react";
import { GetSingleProduct } from "../Api/GetApis";

const DetailProduk = () => {
  const params = useParams()
  const {id} = params
  const hashid = new Hashids("mysecretid", 15);
  const dec = parseInt(hashid.decode(id)); 
  
  useEffect(() => {
    try {
      GetSingleProduct(dec)
      .then((res) => console.log(res));
    } catch (error) {
      console.log(error);
    }
  }, [dec])
  
  return(
    <>
      <Navbar />
    </>
  )
}

export default DetailProduk