//react Library
import { useEffect, useState } from "react";
import { GetProductBylimit } from "../../Api/GetApis";
//Component
import CardProduct from "../../Conponents/CardProduct";

const MostPopular = () => {
  const [datas, setDatas] = useState([]);

  useEffect(() => {
    try {
      GetProductBylimit(5)
      .then((res) => {
        setDatas(res.products);
      });
    } catch (error) {
      console.log(error);
    }
  }, []);
  return (
    <section className="px-44 pb-5">
      <div className="divider mt-0"></div>
      <h1 className="text-2xl font-bold mb-5">Most Popular</h1>
      <div className="grid grid-cols-5 gap-4">
        {datas &&
          datas.map((data, i) => {
            return <CardProduct key={i} data={data} />;
          })}
      </div>
    </section>
  );
};

export default MostPopular;
