import { Swiper, SwiperSlide } from "swiper/react";
import { Pagination, Navigation,Autoplay } from "swiper";
import "swiper/css";
import "swiper/css/pagination";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faAngleLeft, faAngleRight } from "@fortawesome/free-solid-svg-icons";

const HeroSection = () => {
  return (
    <section className="pt-[90px] relative">
      <div className="w-full px-44 py-10">
        <div className="w-full h-fit relative group rounded-xl">
          <button className="btn btn-circle bg-Base hover:bg-gray-300 hover:text-gray-700 border-none next absolute -right-5 opacity-0 group-hover:opacity-100 duration-300 top-[40%] z-10">
            <FontAwesomeIcon icon={faAngleRight} />
          </button>
          <button className="btn btn-circle bg-Base hover:bg-gray-300 hover:text-gray-700 border-none prev absolute -left-5 opacity-0 group-hover:opacity-100 duration-300 top-[40%]  z-10">
            <FontAwesomeIcon icon={faAngleLeft} />
          </button>
          <Swiper
            slidesPerView={1}
            pagination={{
              dynamicBullets: true,
            }}
            navigation={{
              nextEl: ".next",
              prevEl: ".prev",
            }}
            loop={true}
            autoplay={{
              delay: 2500,
            }}
            speed={1000}
            modules={[Pagination, Navigation, Autoplay]}
            className="mySwiper rounded-xl w-full"
          >
            <SwiperSlide>
              <img
                src="https://images.tokopedia.net/img/cache/1208/NsjrJu/2023/2/24/3ffb6b00-a19b-47c3-824d-d31b3e0973cb.jpg.webp?ect=4g"
                alt=""
              />
            </SwiperSlide>
            <SwiperSlide>
              <img
                src="https://images.tokopedia.net/img/cache/1208/NsjrJu/2023/2/27/e5cf55ad-b2ff-48e8-9293-d6f04472f0e2.jpg.webp?ect=4g"
                alt=""
              />
            </SwiperSlide>
            <SwiperSlide>
              <img
                src="https://images.tokopedia.net/img/cache/1208/NsjrJu/2023/2/27/16cd61c9-52ba-4252-ba55-7d5c6f3ec312.jpg.webp?ect=4g"
                alt=""
              />
            </SwiperSlide>
          </Swiper>
        </div>
      </div>
    </section>
  );
};
export default HeroSection;
