//React Library
import { useEffect, useState } from "react";
//Api
import { GetAllProduct, GetByCategory } from "../../Api/GetApis";
//Component
import CardProduct from "../../Conponents/CardProduct"

const ListProduct = ({datas}) => {
  const category = datas.categoryCheked;
  const conditionalBook = datas.bookCheked;
  const [datasProduk, setDatasProduk] = useState([])

  useEffect(() => {
    if(category || conditionalBook){
      GetByCategory(category, 10)
      .then(res => setDatasProduk(res))
    }else{
      GetAllProduct()
      .then(res => setDatasProduk(res))
    }
  }, [category, conditionalBook])

  return (
    <div className="flex flex-col">
      <div className="w-full">
        <h1 className="font-bold">Produk</h1>
        <div className="divider my-0"></div>
      </div>
      <div>
        <p className="text-[14px] my-3">{`menampilkan dari 1 - ${datasProduk?.limit} dari total ${datasProduk?.products?.length} untuk produk`} <span className="font-bold">{category ? category : "All Produk"}</span></p>
      </div>
      <div className="grid grid-cols-4 gap-3">
        {datasProduk?.products && datasProduk?.products.map((data, i) => {
          return(
            <CardProduct key={i} data={data} />
          )
        })}
      </div>
    </div>
  );
}

export default ListProduct