//Icon
import { faStar } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Link } from "react-router-dom";
//Library external
import Hashids from "hashids";

const CardProduct = ({data}) => {
  const hashid = new Hashids("mysecretid", 15)
  const enc = hashid.encode(data.id)

  return (
    <Link to={`/product/${enc}`} className="card w-full bg-base-100 shadow-xl cursor-pointer">
      <div>
        <img
          className="aspect-square rounded-t-xl"
          src={data.thumbnail}
          alt="Shoes"
        />
      </div>
      <div className="p-3 h-full flex flex-col justify-between">
        <h2 className="">{data.title}</h2>
        <div className="flex w-full items-center justify-between pt-5">
          <div className="">
            <p>$ {data.price}</p>
            <div className="avatar flex gap-2 items-center py-1">
              <div className="w-6 rounded-full">
                <img
                  src="https://daisyui.com/images/stock/photo-1534528741775-53994a69daeb.jpg"
                  alt=""
                />
              </div>
              <p className="text-[12px]">{data.brand}</p>
            </div>
          </div>
          <div className="flex items-center gap-1">
            <FontAwesomeIcon icon={faStar} className="text-amber-500" />
            <p>{data.rating}</p>
          </div>
        </div>
      </div>
    </Link>
  );
}

export default CardProduct