//react library
import { useEffect, useState } from "react";
import { GetCategories } from "../../Api/GetApis";
//Library External
import { Collapse } from "antd";

const Filter = ({ datasToParent }) => {
  const [categories, setCategories] = useState([]);
  const [categoryCheked, setCategoryCheked] = useState();
  const [bookCheked, setBookCheked] = useState();
  const { Panel } = Collapse;
  const KondisiBuku = ["baru", "bekas"];

  useEffect(() => {
    try {
      GetCategories().then((res) => {
        setCategories(res);
      });
    } catch (error) {
      console.log(error);
    }
  }, []);

  useEffect(() => {
    try {
      datasToParent({
        categoryCheked,
        bookCheked,
      });
    } catch (error) {
      console.log(error);
    }
  }, [categoryCheked, bookCheked]);

  const handleCategories = (e) => {
    setCategoryCheked(e);
  };

  const handleKondisiBuku = (e) => {
    setBookCheked(e);
  };

  return (
    <div className="flex flex-col gap-2">
      <div className="font-bold">
        <h1>Filter</h1>
      </div>
      <div className="w-[200px] shadow-md rounded">
        <Collapse accordion bordered={false} className="border">
          <Panel
            header="Kategori"
            key="1"
            className="bg-secondary-content rounded-t"
          >
            {categories.map((category, i) => (
              <label
                key={i}
                className="flex items-center"
                onClick={() => handleCategories(category)}
              >
                <input
                  type="checkbox"
                  name="radio"
                  value={category}
                  checked={categoryCheked === category}
                  readOnly
                  className="checkbox bg-secondary-content w-[20px] h-[20px]"
                />
                <p className="cursor-pointer px-2 py-1 rounded">{category}</p>
              </label>
            ))}
          </Panel>
          <Panel header="Rating" key="2" className="bg-secondary-content">
            <label className="rating items-center">
              <input
                type="radio"
                name="rating-2"
                readOnly
                className="mask mask-star-2 w-[20px] bg-orange-400"
              />
              <input
                type="radio"
                name="rating-2"
                readOnly
                className="mask mask-star-2 w-[20px] bg-orange-400"
              />
              <input
                type="radio"
                name="rating-2"
                readOnly
                className="mask mask-star-2 w-[20px] bg-orange-400"
              />
              <input
                type="radio"
                name="rating-2"
                readOnly
                checked
                className="mask mask-star-2 w-[20px] bg-orange-400"
              />
              <input
                type="radio"
                name="rating-2"
                readOnly
                className="mask mask-star-2 w-[20px] bg-orange-400"
              />
              <p className="text-[12px] ml-1">4 keatas</p>
            </label>
          </Panel>
          <Panel header="Kondisi" key="3" className="bg-secondary-content rounded-b">
            {KondisiBuku.map((book, i) => (
              <label
                key={i}
                className="flex items-center"
                onClick={() => handleKondisiBuku(book)}
              >
                <input
                  type="checkbox"
                  name="kondisi"
                  checked={bookCheked === book}
                  readOnly
                  className="checkbox bg-secondary-content w-[20px] h-[20px]"
                />
                <p className="cursor-pointer px-2 py-1 rounded">{book}</p>
              </label>
            ))}
          </Panel>
        </Collapse>
      </div>
    </div>
  );
};

//
export default Filter;
