import axios from "axios"

export const GetCategories = async() =>{
  const res = await axios.get("https://dummyjson.com/products/categories");
  return res.data
}

export const GetByCategory = async(selector, limit) => {
  const res = await axios.get(
    `https://dummyjson.com/products/category/${selector}?limit=${limit}`
  );
  return res.data
}

export const GetProductBylimit = async(limit) => {
  const res = await axios.get(`https://dummyjson.com/products?limit=${limit}`)
  return res.data
}

export const GetAllProduct = async() => {
  const res = await axios.get("https://dummyjson.com/products");
  return res.data
}

export const GetSingleProduct = async(id) => {
  const res = await axios.get(`https://dummyjson.com/products/${id}`);
  return res.data
}