//External Library
import { Autoplay } from "swiper";
import { Swiper, SwiperSlide } from "swiper/react";
//react Library
import { useEffect, useState } from "react";
//Component
import CardProduct from "../../Conponents/CardProduct";
import { Link } from "react-router-dom";
import { GetByCategory, GetCategories } from "../../Api/GetApis";

const ListProductHome = () => {
  const [datas, setDatas] = useState([]);
  const [categories, setCategories] = useState([]);
  const [categorySelect, setCategorySelect] = useState("smartphones");

  useEffect(() => {
    try {
      GetCategories().then((res) => {
        setCategories(res);
      });
    } catch (error) {
      console.log(error);
    }
  }, []);

  useEffect(() => {
    try {
        GetByCategory(categorySelect, 5)
        .then((res) => {
          setDatas(res.products);
        });
    } catch (error) {
      console.log(error);
    }
  }, [categorySelect]);

  const HandleCategorySelector = (value) => {
    setCategorySelect(value)
  };

  return (
    <section className="px-44 pb-5 w-full">
      <div className="divider mt-5"></div>
      <h1 className="text-2xl font-bold ">For You</h1>
      <div className="my-3 w-full">
        <Swiper
          spaceBetween={10}
          slidesPerView={"auto"}
          loop={true}
          autoplay={{
            delay: 2500,
          }}
          speed={1000}
          modules={[Autoplay]}
          className="mySwiper rounded"
        >
          {categories &&
            categories.map((category, i) => {
              return (
                <SwiperSlide key={i} style={{ width: "160px" }}>
                  <div
                    onClick={() => HandleCategorySelector(category)}
                    className="btn bg-Base text-start hover:bg-gray-500 border-none w-40 h-16 flex justify-start items-start py-3"
                  >
                    <p>{category}</p>
                  </div>
                </SwiperSlide>
              );
            })}
        </Swiper>
      </div>
      <div className="grid grid-cols-5 gap-4">
        {datas &&
          datas.map((data, i) => {
            return <CardProduct key={i} data={data} />;
          })}
      </div>
      <div className="w-full flex justify-center">
        <Link to={"product"} className="btn bg-Base text-start hover:bg-gray-500 border-none mt-10">
          Muat Lebih Banyak
        </Link>
      </div>
    </section>
  );
};

export default ListProductHome;
